var _this;
export default {
	data() {
		return {
			USER_ROLE: null,
			Menu_User: this.Menu_User,
			Menu_Admin: this.Menu_Admin,
			No_Login : [{
				title: '用户确认',
				name: 'noLogin',
				color: 'pink',
				cuIcon: 'same'
			}, {
				title: '测试页',
				name: 'testPage',
				color: 'mauve',
				cuIcon: 'exit'
			}],
			dotStyle: false,
			swiperList: [{
				id: 0,
				type: 'image',
				main_img_url:"",
				title: '浙北智慧公寓'
			}],
			notice:'暂无公告'
		}
	},
	mounted() {
		_this = this;
		_this.USER_ROLE = uni.getStorageSync("USERROLE");
		var allParams = {
			url: 'queryAllNoticeInfoByLimit',
			setUpUrl: true,
			data:{
				page:1,
				limit:1
			},
			type: 'get',
			sCallback: function(data) {
				if (data.code == 1) {
					uni.showToast({
						title: '获取公告数据失败',
						icon: 'none'
					})
				} else {
					if (data.data==null || data.data.length == 0) {
						_this.notice='暂无公告';
					} else {
						_this.notice=data.data[0].info;
					}
		
				}
			}
		};
		_this.$base.request(allParams, _this);
	},
	methods: {
		getMore(){
			uni.navigateTo({
			    url: '../notice/notice'
			});
		},
	}
}
